<template>
  <s-list group :class="classes">
    <div
      class="choice"
      :class="{ 'choice--custom': custom, 'choice--native': native }"
      v-for="(option, index) in options"
      :key="`option--${index}`"
    >
      <input
        :name="name"
        type="radio"
        :id="optionId(option)"
        :value="option.value"
        :checked="option.selected"
        v-model="checked"
        @change="input"
      />

      <!-- STYLE: BUTTON -->
      <s-button
        v-if="component == 'button'"
        v-bind="$attrs"
        :for="optionId(option)"
        :active="checked == option.value"
      >{{ option.label }}</s-button>

      <!-- STYLE: NATIVE OR CUSTOM -->
      <label
        v-else
        class="choice__label"
        :class="{ 'choice__label--checked': checked == option.value }"
        :for="optionId(option)"
      >
        <slot
          :name="optionId(option)"
          :option="option"
          :checked="checked == option.value"
        >{{ option.label }}</slot>
      </label>
    </div>
  </s-list>
</template>

<script>
import { uid } from "../utils";

export default {
  name: "s-radio",
  inheritAttrs: false,

  data() {
    return {
      checked: this.value
    };
  },

  watch: {
    value(newValue) {
      this.checked = newValue;
    }
  },

  props: {
    value: [String, Number, Boolean],
    options: Array,
    name: String,
    component: String,
    gap: {
      type: String,
      default: "md"
    },
    custom: {
      type: Boolean,
      default: false
    },
    native: {
      type: Boolean,
      default: false
    },
    buttonInactiveStyle: {
      type: String,
      default: "outline"
    }
  },

  computed: {
    uniqueId() {
      return uid();
    },
    classes() {
      const classes = [];
      if (this.component == "button") {
        classes.push("buttons", `buttons--group`);
      }
      return classes;
    }
  },

  methods: {
    input() {
      let metaValue = this.options.find(item => item.value == this.checked);
      this.$emit("input", this.checked, metaValue);
    },

    optionId(option) {
      return option.id || this.uniqueId + option.value;
    }
  }
};
</script>
